body {
     background-color: #FFF5E6; /* Cremita */
     margin: 0;
     font-family: 'Lora', serif; /* Lucida Bright alternativa */
   }

   .App {
     text-align: center;
   }

   .welcome-banner {
     background-color: #FFF5E6; /* Cremita */
     padding: 30px;
     margin-top: 100px;
     margin-bottom: 55px;
     display: flex;
     align-items: center;
     justify-content: center;
     gap: 20px;
   }

   .welcome-banner h1 {
     color: #5A0015; /* Vino tinto */
     margin: 0;
     font-size: 55px;
     font-family: 'Bodoni Moda', serif; /* Didot alternativa */
     letter-spacing: 2px;
   }

   .welcome-banner .banner-icon {
     color: #5A0015; /* Vino tinto */
     font-size: 50px;
   }

   .navbar {
     width: 600px; /* Ancho similar al ejemplo */
     margin: 0 auto;
   }

   .menu-grid {
     display: grid;
     grid-template-columns: repeat(2, 1fr);
     gap: 20px;
   }

   .menu-item {
     background-color: #5A0015; /* Vino tinto */
     border-radius: 10px;
     border: 2px solid #5A0015; /* Borde vino tinto */
     padding: 5px;
     text-align: center;
   }

   .menu-item a {
     background-color: #FFF5E6; /* Cremita */
     color: #5A0015; /* Vino tinto */
     text-decoration: none;
     font-size: 18px;
     font-family: 'Lora', serif; /* Lucida Bright alternativa */
     display: block;
     padding: 15px;
     border-radius: 8px;
     transition: background-color 0.3s ease;
     display: flex;
     align-items: center;
     justify-content: center;
     gap: 10px;
   }

   .menu-item a:hover {
     background-color: #FFE8D6; /* Cremita más oscuro para hover */
   }

   .menu-item i {
     font-size: 16px;
   }

   .auth-links {
     position: absolute;
     top: 15px;
     right: 25px;
     display: flex;
     gap: 20px;
   }

   .auth-link {
     color: #5A0015; /* Vino tinto */
     text-decoration: none;
     font-size: 18px;
     display: flex;
     align-items: center;
     gap: 8px;
     font-family: 'Lora', serif; /* Lucida Bright alternativa */
   }

   .auth-link:hover {
     color: #800020; /* Vino más claro */
   }

   .auth-link i {
     font-size: 16px;
   }

   .home {
     display: flex;
     flex-direction: column;
     min-height: 100vh;
     align-items: center;
     justify-content: flex-start;
   }

   .page {
     padding: 20px;
   }

   .page h2 {
     color: #5A0015; /* Vino tinto */
   }

   ul {
     list-style: none;
     padding: 0;
   }

   li {
     background: #FFF5E6; /* Cremita */
     margin: 10px auto;
     padding: 10px;
     width: 80%;
     border: 1px solid #5A0015; /* Vino tinto */
     border-radius: 5px;
   }
